.uploadPage {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
  font-family: 'Arial', sans-serif;
  height: 100vh;
  overflow: scroll;
}

.uploadContainer, .uploadsContainer {
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 30px;
  margin-bottom: 40px;
}

.pageTitle, .uploadsTitle {
  color: #333;
  text-align: center;
  margin-bottom: 30px;
  font-size: 24px;
}

.formContainer {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.inputGroup {
  display: flex;
  flex-direction: column;
}

.inputGroup label {
  margin-bottom: 5px;
  color: #555;
}

.inputGroup input {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
}

.dropZone {
  border: 2px dashed #007bff;
  border-radius: 8px;
  padding: 40px;
  text-align: center;
  color: #555;
  background-color: #f8f9fa;
  cursor: pointer;
  transition: all 0.3s ease;
}

.dropZone:hover {
  background-color: #e9ecef;
}

.dropZone.hasFile {
  border-color: #28a745;
  background-color: #e9f7ef;
}

.uploadIcon, .fileIcon {
  font-size: 48px;
  color: #007bff;
  margin-bottom: 10px;
}

.fileIcon {
  color: #28a745;
}

.fileName {
  font-weight: bold;
  color: #28a745;
}

.fileInput {
  display: none;
}

.submitButton, .replaceButton {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 12px 20px;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  justify-content: center;
}

.submitButton:hover, .replaceButton:hover {
  background-color: #0056b3;
}

.submitButton:disabled, .replaceButton:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
}

.tableContainer {
  overflow-x: auto;
}

.uploadsTable {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
}

.uploadsTable th, .uploadsTable td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #dee2e6;
}

.uploadsTable th {
  background-color: #f8f9fa;
  font-weight: bold;
  color: #495057;
}

.uploadsTable tr:last-child td {
  border-bottom: none;
}

.uploadsTable tr:hover {
  background-color: #f1f3f5;
}

.viewPdf {
  color: #007bff;
  text-decoration: none;
}

.viewPdf:hover {
  text-decoration: underline;
}

.status {
  display: inline-block;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 14px;
  font-weight: bold;
}

.status.pending {
  background-color: #ffeeba;
  color: #856404;
}

.status.approved {
  background-color: #d4edda;
  color: #155724;
}

.status.rejected {
  background-color: #f8d7da;
  color: #721c24;
}

.noFiles {
  text-align: center;
  color: #6c757d;
  font-style: italic;
}

.modalContent {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

@media (max-width: 768px) {
  .uploadContainer, .uploadsContainer {
    padding: 20px;
  }

  .dropZone {
    padding: 20px;
  }

  .uploadsTable th, .uploadsTable td {
    padding: 8px;
  }

  .submitButton, .replaceButton {
    font-size: 14px;
    padding: 10px 16px;
  }
}

/* Ant Design Modal Overrides */
:global(.ant-modal-content) {
  border-radius: 8px;
}

:global(.ant-modal-header) {
  border-radius: 8px 8px 0 0;
}

:global(.ant-modal-title) {
  font-size: 18px;
  color: #333;
}

:global(.ant-btn) {
  border-radius: 4px;
}

:global(.ant-btn-primary) {
  background-color: #007bff;
  border-color: #007bff;
}

:global(.ant-btn-primary:hover) {
  background-color: #0056b3;
  border-color: #0056b3;
}
.status.cancelled {
  background: #ecacac;
  border: 1px solid #d9d9d9;
}
.cancelled {
  color: #ff0000;
}


.registerButton {
  margin-left: 10px;
  background-color: #1890ff;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
}

.registerButton:hover {
  background-color: #40a9ff;
}

