span.ant-select-selection-item .ant-select-selection-item-content {
  color: #f00 !important;
}

.colorLegend {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
}

h3 {
  color: #333;
  margin-bottom: 1rem;
  font-weight: bold;
  font-size: 1.5rem;
  position: relative;
  padding-left: 15px;
}

h3::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 4px;
  height: 70%;
  background-color: #3498db;
  border-radius: 2px;
}

h3.pageTitle {
  font-size: 2rem;
  border-bottom: 2px solid #3498db;
  padding-bottom: 5px;
  padding-left: 0;
}

h3.pageTitle::before {
  display: none;
}
.legendItem {
  display: flex;
  align-items: center;
  margin-left: 20px;
  font-size: 13px;
}


.ant-drawer .ant-drawer-header {
  margin-top: 21px;
}

.uploadsTitle::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 5px;
  height: 70%;
  background-color: #3498db;
  border-radius: 2px;
}

.colorBox {
  width: 17px;
  height: 17px;
  border-radius: 50%;
  margin-right: 5px;
  /* border: 1px solid #000; */
}

.tableRow {
  transition: background-color 0.3s;
}

.approvedRow {
  background-color: #90ee90;
}

.pendingRow {
  background-color: #ffe5b4;
}

.cancelledRow {
  background-color: #ffcccb;
}


